import styled from "styled-components";
import Phone from "../../assets/images/Elements/phone.png";

import { VideoBtn } from "../../assets/images/Elements/VideoBtn";

const IntroPhone = styled.div`
  display: flex;
  justify-content: flex-end;
  width: 100%;
  max-width: 24.125rem;
  height: fit-content;
  margin: 0 auto;
  position: relative;

  & > img {
    position: relative;
    top: 0;
    left: 0;
    z-index: 2;
    width: 100%;
    max-width: 20.375rem;
    max-height: 23.563rem;
  }
`;

const ButtonVideo = styled.a`
  position: absolute;
  left: 0;
  bottom: 0;
  width: 34.9vw;
  height: 34.9vw;
  max-width: 8.5rem;
  max-height: 8.5rem;
  border-radius: 12.5rem;

  background: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;

  user-select: none;

  & div {
    position: relative;
    width: 90%;
    height: 90%;
    display: flex;
    justify-content: center;
    align-items: center;

    & svg {
      position: absolute;
      left: calc(50% - 15px);
      width: 30%;
    }

    & img {
      width: 100%;
      animation: rotate infinite 29s linear;
    }

    @keyframes rotate {
      0% {
        transform: rotate(0);
      }
      100% {
        transform: rotate(180deg);
      }
    }
  }
`;

const PhoneMobile = () => {
  return (
    <IntroPhone>
      <ButtonVideo href="#">
        <VideoBtn />
      </ButtonVideo>
      <img src={Phone} alt="Phones" loading="lazy" />
    </IntroPhone>
  );
};

export default PhoneMobile;
