import { Title1 } from "../../assets/styles/component";
import styled from "styled-components";

import { YouTube } from "../../assets/images/Socials/Youtube";
import { Odnoklassniki } from "../../assets/images/Socials/Odnoklassniki";
import { Vk } from "../../assets/images/Socials/Vk";
import { Telegram } from "../../assets/images/Socials/Telegram";
import { Instagram } from "../../assets/images/Socials/Instagram";
import { YandexZen } from "../../assets/images/Socials/YandexZen";

import Tag from "./tagMobile";

import { EngLoc } from "../../assets/constants/localizations";

const SDescription = styled.div`
  width: 100%;
  text-align: left;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.563rem;
  justify-content: space-between;
`;

const IntroTitle = styled(Title1)`
  max-width: 38.75rem;
  font-size: clamp(2rem, 5vw, 2.5rem);
  font-weight: 400;
  font-size: 2.25rem;
  line-height: 110%;
`;

const TagCloud = styled.div`
  display: flex;
  width: 100%;
  max-width: 38.75rem;

  gap:0.313rem;
  flex-wrap: wrap;
`;

const SText = styled.p`
  max-width: 38.75rem;
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 1.063rem;
  color: #fff;
`;

const Description = ({ language }) => {
  const Tags = [
    {
      text:
        language === "Ru" ? `Реклама в соцсетях ` : EngLoc.intro.tags.telegram,
      socials: [
        <Vk key={1} width="14" height="14" />,
        <Instagram key={2} width="14" height="14" />,
        <Odnoklassniki key={3} width="14" height="14" />,
        <YouTube key={4} width="14" height="14" />,
        <YandexZen key={5} width="14" height="14" />,
      ],
    },
    {
      text: language === "Ru" ? `Реклама в мессенджерах` : EngLoc.intro.tags.vk,
      socials: [<Telegram key={6} width="14" height="14" />],
    },
    {
      text:
        language === "Ru"
          ? "Видеореклама на сайтах"
          : EngLoc.intro.tags.odnoclassniki,
      socials: [],
    },
    {
      text:
        language === "Ru"
          ? "CPA реклама у блоггеров"
          : EngLoc.intro.tags.Video_Ads,
      socials: [],
    },
    {
      text:
        language === "Ru" ? "Реклама в стримах" : EngLoc.intro.tags.Analytics,
      socials: [],
    },
  ];

  return (
    <SDescription>
      <IntroTitle weight="300">
        {language === "Ru"
          ? "Технологические решения для вашего"
          : EngLoc.intro.title}
        {language === "Ru" ? " маркетинга" : EngLoc.intro.title_span}
      </IntroTitle>
      <SText>
        От точечных задач малого и среднего бизнеса до многомиллионных охватов
        глобальных брендов и агентств
      </SText>
      <TagCloud>
        {Tags.map((tag, index) => (
          <Tag key={index}>
            {tag.text} {tag.socials && tag.socials}
          </Tag>
        ))}
      </TagCloud>
    </SDescription>
  );
};

export default Description;
